<!--
    echart : 饼状图
    陈斌
-->
<template>
    <div class="card">
        <div class="card-body">
            <div class="row">

                <h3 class="card-title mb-4 text-muted" style="font-size: 20px">主机风险等级分布</h3>
                <div id="chartPie" class="pie-wrap" style="width:100%; height:550px;"></div>

            </div>
        </div>

    </div>

</template>

<script>
    import * as echarts from 'echarts';
    require('echarts/theme/macarons');//引入主题

    export default {
        data() {
            return {
                chartPie: null
            }
        },
        mounted() {
            this.$nextTick(() => {
                this.drawPieChart();
            })
        },
        methods: {
            drawPieChart() {
                // 初始化echart
                this.chartPie = echarts.init(document.getElementById('chartPie'),'macarons');
                this.chartPie.setOption({
                    // 配置可选项
                    legend: {
                        top: 'bottom'
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            mark: {show: true},
                            dataView: {show: true, readOnly: false},
                            restore: {show: true},
                            saveAsImage: {show: true},
                        },
                    },
                    series: [
                        {
                            name: '面积模式',
                            type: 'pie',
                            radius: [50, 200],
                            center: ['50%', '50%'],
                            roseType: 'area',
                            // 颜色分级
                            color:['#ee6666',
                                '#fc8452',
                                '#5470c6',
                                '#91cc75'],
                            itemStyle: {
                                borderRadius: 16
                            },
                            data: [
                                {value: 70, name: '非常危险'},
                                {value: 38, name: '比较危险'},
                                {value: 20, name: '比较安全'},
                                {value: 6, name: '安全'},

                            ]
                        }
                    ],
                    textStyle: {
                        fontSize : 16
                    }
                });
            }
        }
    }
</script>
