<script>
  /**
   *  陈斌
   *
   */
import axios from "axios";

/**
 * 最近活动记录
 */
export default {
  data() {
    return {
      trackData: '',
      activityData: [
        {
          icon: "ri-edit-2-fill",
          date: "28 Apr, 2020 ",
          time: "12:07 am",
          title: "用户user提交了基线检测"
        },
        {
          icon: "ri-user-2-fill",
          date: "21 Apr, 2020 ",
          time: "08:01 pm",
          title: "用户Admin提交了计算机基础信息检测"
        },
        {
          icon: "ri-bar-chart-fill",
          date: "17 Apr, 2020 ",
          time: "05:10 pm",
          title: "用户Homage提交了端口扫描检测任务"
        },
        {
          icon: "ri-calendar-2-fill",
          date: "07 Apr, 2020",
          time: "12:47 pm",
          title: "用户Hact提交了自启动项服务检测任务"
        },
        {
          icon: "ri-edit-2-fill",
          date: "05 Apr, 2020 ",
          time: "03:09 pm",
          title: "用户Hact提交了自启动项服务检测任务"
        },
        {
          icon: "ri-user-2-fill",
          date: "02 Apr, 2020 ",
          time: "12:07 am",
          title: "用户Hact提交了自启动项服务检测任务"
        }
      ]
    };
  },

  methods: {

    getTrack() {
      //get为请求方式，请求路径为baseURL+''即http://localhost:8088/student/index
      //then回调函数，res为请求返回的结果
      axios.get('http://127.0.0.1:8848/tasks/selectAll')
              .then(res => {
                console.log('获取所有结果: ' + res);
                // 获取到数据, 再将其处理为我们需要的格式
                this.trackData = res.data.list;
                // statData为我们需要 动态渲染的数据

              }).catch(function(error) {
        console.log(error);
      })


    },


  },
  // mounted: function () {
  //   this.getStudent();
  // },
  created: function () {
    this.getTrack();
  }


};
</script>

<template>
  <div class="card">
    <div class="card-body">
<!--      <div>{{trackData}}</div>-->
      <b-dropdown right toggle-class="arrow-none card-drop" class="float-right" variant="white">
        <template v-slot:button-content>
          <i class="mdi mdi-dots-vertical"></i>
        </template>
        <!-- item-->
        <b-dropdown-item>一天内</b-dropdown-item>
        <!-- item-->
        <b-dropdown-item>一周内</b-dropdown-item>
        <!-- item-->
        <b-dropdown-item>一个月内</b-dropdown-item>
      </b-dropdown>

      <h3 class="card-title mb-4 text-muted" style="font-size: 20px">最近检测记录</h3>

      <div data-simplebar style="max-height: 550px;">
        <ul class="list-unstyled activity-wid">
          <li class="activity-list" v-for="(data, index) in activityData" :key="index">
            <div class="activity-icon avatar-xs">
              <span class="avatar-title bg-soft-primary text-primary rounded-circle">
                <i :class="`${data.icon}`"></i>
              </span>
            </div>
            <div>
              <div>
                <h5 class="font-size-16">
                  {{data.date}}
                  <small class="text-muted font-size-14">{{data.time}}</small>
                </h5>
              </div>

              <div>
                <p class="text-muted mb-0 font-size-18">{{data.title}}</p>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>