<script>
/**
 *  陈斌
 */
/**
 *  结果报告样例
 */
export default {
  data() {
    return {
      transactionData: [
        {
          orderid: "#基线检测结果",
          date: "28 Mar, 2020",
          billingname: "Windows 10",
          total: "164",
          paymentstatus: "中风险",
            taskid: ''
        },
        {
          orderid: "#基线检测结果",
          date: "28 Mar, 2020",
          billingname: "Windows Server 2008",
          total: "141",
          paymentstatus: "低风险",
            taskid: ''
        },
        {
          orderid: "#主机基本信息检测",
          date: "29 Mar, 2020",
          billingname: "Window 10",
          total: "24",
          paymentstatus: "低风险",
            taskid: ''
        },
        {
          orderid: "#主机服务检测",
          date: "30 Mar, 2020",
          billingname: "Windows 10",
          total: "44",
          paymentstatus: "低风险",
            taskid: ''
        },
        {
          orderid: "#主机服务检测",
          date: "31 Mar, 2020",
          billingname: "Windows Server 2008",
          total: "49",
          paymentstatus: "中风险",
            taskid: ''
        },
        {
          orderid: "#主机基本信息检测",
          date: "01 Apr, 2020",
          billingname: "Windows Server 2008",
          total: "160",
          paymentstatus: "高风险",
            taskid: ''
        },
        {
          orderid: "#主机基本信息检测",
          date: "01 Apr, 2020",
          billingname: "Windows Server 2008",
          total: "160",
          paymentstatus: "高风险",
            taskid: ''
        },
        {
          orderid: "#主机服务检测",
          date: "30 Mar, 2020",
          billingname: "Windows 10",
          total: "44",
          paymentstatus: "低风险",
            taskid: ''
        },
        {
          orderid: "#主机服务检测",
          date: "30 Mar, 2020",
          billingname: "Windows 10",
          total: "44",
          paymentstatus: "低风险",
            taskid: ''
        },

      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 25, 50],
      filter: null,
      filterOn: [],
      sortBy: "orderid",
      sortDesc: false,
      fields: [
        { key: "orderid", sortable: true, label: "检测项目" },
        { key: "date", sortable: true },
        { key: "billingname", sortable: true, label: "检测主机" },
        { key: "total", sortable: true, label: "总条目数" },
        { key: "paymentstatus", sortable: true, label: "安全状态" },
        { key: "action" , label: "查看"}
      ]
    };
  },
  computed: {
    /**
     * Total no. of records
     */
    rows() {
      return this.transactionData.length;
    }
  },
  mounted() {
    // Set the initial number of items
    this.totalRows = this.transactionData.length;
  },
  methods: {
    /**
     * Search the table data with search input
     */
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
      goProfile(){
          this.$router.push('/check_result')
      },

  }
};
</script>

<template>
  <div class="card">
    <div class="card-body" >
      <b-dropdown right toggle-class="arrow-none card-drop" class="float-right" variant="white">
        <template v-slot:button-content>
          <i class="mdi mdi-dots-vertical"></i>
        </template>
      </b-dropdown>

      <h4 class="card-title mb-4" style="font-size: 20px">检测结果样例</h4>
      <div class="row mt-4">
        <div class="col-sm-12 col-md-6">
          <div id="tickets-table_length" class="dataTables_length">
            <label class="d-inline-flex align-items-center">
              Show&nbsp;
              <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
            </label>
          </div>
        </div>
        <!-- Search -->
        <div class="col-sm-12 col-md-6">
          <div id="tickets-table_filter" class="dataTables_filter text-md-right">
            <label class="d-inline-flex align-items-center">
<!--              查找接口-->
              Search:
              <b-form-input
                v-model="filter"
                type="search"
                class="form-control form-control-sm ml-2"
              ></b-form-input>
            </label>
          </div>
        </div>
        <!-- End search -->
      </div>
      <div class="table-responsive">
        <b-table
          :items="transactionData"
          :fields="fields"
          responsive="sm"
          :per-page="perPage"
          :current-page="currentPage"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :filter="filter"
          :filter-included-fields="filterOn"
          @filtered="onFiltered"
        >
          <template v-slot:cell(paymentstatus)="row">
            <div
              class="badge font-size-12"
              :class="{'badge-soft-danger': `${row.value}` === '高风险',
              'badge-soft-warning': `${row.value}` === '中风险',
              'badge-soft-success': `${row.value}` === '低风险'
              }"
            >{{ row.value}}</div>
          </template>

          <template v-slot:cell(action)>
<!--            修改为查看接口-->
            <a
              @click = "goProfile()"
              class="mr-3 text-primary"
              v-b-tooltip.hover
              data-toggle="tooltip"
              title="Edit"
            >
              <i class="mdi mdi-pencil font-size-18"></i>
            </a>

<!--            <a href="javascript:void(0);" class="text-danger" v-b-tooltip.hover title="Delete">-->

<!--              <i class="mdi mdi-trash-can font-size-18"></i>-->
<!--            </a>-->

          </template>
        </b-table>
      </div>
      <div class="row">
        <div class="col">
          <div class="dataTables_paginate paging_simple_numbers float-right">
            <ul class="pagination pagination-rounded mb-0">
              <!-- pagination -->
              <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>