  export default {
    data() {
      return {
        treePie: null,
      }

    },
    mounted() {
      this.$nextTick(() => {
        this.drawPieChart();
      })
    },
    methods: {
      drawPieChart() {

        // 初始化树, macarons为引用的主题
        this.treePie = echarts.init(document.getElementById('treePie'),'macarons');

        // 隐式加载
        this.treePie.hideLoading();
        this.treePie.setOption({
          tooltip: {
            trigger: 'item',
            triggerOn: 'mousemove'
          },
          textStyle: {
            fontSize: 16
          },
          legend: {
            top: '2%',
            left: '3%',
            orient: 'vertical',
            data: [{
              name: '基线配置风险排行',
              icon: 'rectangle'
            },
              {
                name: '服务配置风险排行',
                icon: 'rectangle'
              }],
            borderColor: '#c23531'
          },
          series:[
            {
              type: 'tree',

              name: '基线配置风险排行',

              // eslint-disable-next-line no-undef
              data: [tree_data1],

              top: '5%',
              left: '8%',
              bottom: '2%',
              right: '60%',

              symbolSize: 10,

              label: {
                position: 'left',
                verticalAlign: 'middle',
                align: 'right'
              },

              leaves: {
                label: {
                  position: 'right',
                  verticalAlign: 'middle',
                  align: 'left'
                }
              },

              emphasis: {
                focus: 'descendant'
              },

              expandAndCollapse: true,

              animationDuration: 750,
              animationDurationUpdate: 950

