<script>
  // eslint-disable-next-line no-unused-vars
  import axios from 'axios'
/**
 *  陈斌
 *  数值型统计
 */
export default {
  data() {

    return {
      memberData: '',
      statData: ''
    };
  },

  methods: {

    getMember() {
      //get为请求方式，请求路径为baseURL+''即http://localhost:8088/student/index
      //then回调函数，res为请求返回的结果
      axios.get('http://82.156.8.5:8848/host2task/getAnalysisResult?taskId=1')
              .then(res => {
        console.log(res);
        // 获取到数据, 再将其处理为我们需要的格式
        this.memberData = res.data.checkResult;
        // statData为我们需要 动态渲染的数据
        this.statData = [
          {
            title: "核查总条目",
            icon: "ri-stack-line",
            value: parseInt(this.memberData['核查总条目数']) * 47,
            subvalue: "100 %"
          },
          {
            title: "不合格条目",
            icon: "ri-store-2-line",
            value: parseInt(this.memberData['不合格条目数']) * 47,
            subvalue: " 70 % "
          },
          {
            title: "合格条目",
            icon: "ri-briefcase-4-line",
            value: parseInt(this.memberData['合格条目数']) * 47,
            subvalue: " 10% "
          },
        ]

      }).catch(function(error) {
        console.log(error);
      })

    },


  },
  // mounted: function () {
  //   this.getStudent();
  // },
  created: function () {
    this.getMember();
  }

};
</script>

<template>
  <div class="row">
    <div class="col-md-4" v-for="(data, index) in statData" :key="index">
      <div class="card">
        <div class="card-body">
          <div class="media">
            <div class="media-body overflow-hidden">
              <p class="text-truncate font-size-18 mb-6 ml-3">{{data.title}}</p>
<!--              <p class="text-truncate font-size-18 mb-6 ml-3">{{memberData['合格项目数']}}</p>-->
              <h4 class="mb-0 ml-3">{{data.value}}</h4>
            </div>
            <div class="text-primary">
              <i :class="`${data.icon} font-size-48`"></i>
            </div>
          </div>
        </div>

        <div class="card-body border-top py-3">
          <div class="text-truncate">
            <span class="badge badge-soft-success font-size-16 ml-3">
              <i class="mdi mdi-menu-up"></i>
              {{data.subvalue}}
            </span>
            <span class="text-muted ml-2 font-size-8">From 2021-06-21</span>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- end row -->
</template>